// loadCategories.js

// 
const retry = async (fn, retries = 3, delay = 1000) => {
  let lastError;
  
  for (let i = 0; i < retries; i++) {
    try {
      return await fn();
    } catch (error) {
      console.log(` ${i + 1} ...`);
      lastError = error;
      if (i < retries - 1) {
        await new Promise(resolve => setTimeout(resolve, delay * (i + 1)));
      }
    }
  }
  
  throw lastError;
};

//  Airtable 
const validateAirtableResponse = (data) => {
  if (!data || !Array.isArray(data.records)) {
    throw new Error(' Airtable ');
  }
  
  return data; // 
};

// 
const parseCategoryData = (records) => {
  const uniqueCategories = new Set();
  let hasValidCategory = false;

  records.forEach(record => {
    const category = record.fields[''];
    if (category) {
      hasValidCategory = true;
      // 
      if (Array.isArray(category)) {
        category.forEach(item => {
          if (item && typeof item === 'string') {
            item.split(/[,;]\s*/).forEach(v => {
              const trimmed = v.trim();
              if (trimmed) {
                uniqueCategories.add(trimmed);
              }
            });
          }
        });
      } else {
        const values = category.toString().split(/[,;]\s*/);
        values.forEach(v => {
          const trimmed = v.trim();
          if (trimmed) {
            uniqueCategories.add(trimmed);
          }
        });
      }
    }
  });

  if (!hasValidCategory) {
    throw new Error('');
  }

  return Array.from(uniqueCategories);
};

// 
const loadCategories = async (envConfig) => {
  if (!envConfig?.REACT_APP_AIRTABLE_API_KEY || !envConfig?.REACT_APP_AIRTABLE_BASE_ID) {
    throw new Error('');
  }

  const fetchCategories = async () => {
    console.log('...');
    
    // 
    let allRecords = [];
    let offset = null;
    let pageCount = 0;
    
    do {
      pageCount++;
      console.log(` ${pageCount} ...`);
      
      //  URL
      let url = `https://api.airtable.com/v0/${envConfig.REACT_APP_AIRTABLE_BASE_ID}/design`;
      const params = new URLSearchParams();
      
      // 
      params.append('_', Date.now());
      
      // 
      params.append('pageSize', '100');
      
      // 
      if (offset) {
        params.append('offset', offset);
      }
      
      const requestUrl = `${url}?${params.toString()}`;
      
      const response = await fetch(requestUrl, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${envConfig.REACT_APP_AIRTABLE_API_KEY}`
        }
      });

      if (!response.ok) {
        const errorText = await response.text().catch(() => '');
        console.error('API :', {
          status: response.status,
          statusText: response.statusText,
          error: errorText
        });
        throw new Error(`API  (${response.status}): ${errorText}`);
      }

      const data = await response.json();
      
      // 
      const validatedData = validateAirtableResponse(data);
      
      // 
      if (validatedData.records && validatedData.records.length > 0) {
        allRecords = [...allRecords, ...validatedData.records];
        console.log(` ${allRecords.length} `);
      }
      
      // 
      offset = validatedData.offset || null;
      
    } while (offset); // 
    
    console.log(` ${pageCount} ${allRecords.length} `);
    
    // 
    if (allRecords.length === 0) {
      throw new Error('');
    }
    
    // 
    const categories = parseCategoryData(allRecords);
    
    if (categories.length === 0) {
      throw new Error('');
    }

    console.log(` ${categories.length} `);
    return categories;
  };

  try {
    return await retry(fetchCategories);
  } catch (error) {
    console.error(':', error);
    
    // 
    if (error.message.includes('API ')) {
      if (error.message.includes('401')) {
        throw new Error(' API ');
      } else if (error.message.includes('403')) {
        throw new Error(' API ');
      } else if (error.message.includes('429')) {
        throw new Error('');
      } else {
        throw new Error('');
      }
    } else if (error.message.includes('')) {
      throw new Error('');
    } else if (error.name === 'TypeError' && error.message === 'Failed to fetch') {
      throw new Error('');
    } else {
      throw new Error('');
    }
  }
};

export default loadCategories;