// App.js  - 
import React, { useState, useEffect, Suspense } from 'react';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import ProductContent from './components/ProductContent';
import { ListProvider } from './components/ListContext';
import Auth from './components/Auth';
import { logger } from './config/config';
import { AuthProvider } from './components/AuthContext';
import ScreenshotPrevention from './components/ScreenshotPrevention'; // 
import './styles/screenshotPrevention.css';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { EnhancedToastProvider } from './components/EnhancedToastContext'; // 


// 
const AdminManagement = React.lazy(() => import('./components/IntegratedManagement'));

const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      refetchOnWindowFocus: false,
      retry: 2,
      staleTime: 5 * 60 * 1000,
      cacheTime: 30 * 60 * 1000,
    },
  },
});

function App() {
  const [user, setUser] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [showAdmin, setShowAdmin] = useState(false);
  const [deviceStatus, setDeviceStatus] = useState({
    isMobileDevice: false,    // 
    isSmallScreen: false,     // 
    shouldBlockAccess: false  // 
  });
  const [deviceInfo, setDeviceInfo] = useState({
    type: '',
    userAgent: navigator.userAgent,
    screenSize: `${window.innerWidth}x${window.innerHeight}`,
    touchEnabled: 'checking...'
  });

  useEffect(() => {
    // 
    const checkDeviceAndScreen = () => {
      // 
      const userAgent = navigator.userAgent.toLowerCase();
      const mobileKeywords = ['android', 'iphone', 'ipad', 'ipod', 'blackberry', 'windows phone', 'mobile'];
      const isMobileByUA = mobileKeywords.some(keyword => userAgent.includes(keyword));
      
      // 
      const isTouchDevice = 'ontouchstart' in window || 
                           navigator.maxTouchPoints > 0 ||
                           navigator.msMaxTouchPoints > 0;
                           
      // 
      const hasOrientationSupport = !!window.DeviceOrientationEvent;
      
      //  -  1000x700
      const isSmallScreen = window.innerWidth < 1000 || window.innerHeight < 700;
      
      //  ()
      let mobileFeatureCount = 0;
      if (isMobileByUA) mobileFeatureCount++;
      if (isTouchDevice) mobileFeatureCount++;
      if (hasOrientationSupport && userAgent.indexOf('mac os') === -1) mobileFeatureCount++; //  Mac 
      
      const isMobileDevice = mobileFeatureCount >= 2;
      
      // 
      setDeviceInfo({
        type: isMobileDevice ? '' : '',
        userAgent: navigator.userAgent,
        screenSize: `${window.innerWidth}x${window.innerHeight}`,
        touchEnabled: isTouchDevice ? '' : '',
        hasSmallScreen: isSmallScreen ? '' : ''
      });
      
      // 
      const shouldBlockAccess = isMobileDevice && isSmallScreen;
      
      setDeviceStatus({
        isMobileDevice,
        isSmallScreen,
        shouldBlockAccess
      });
    };

    // 
    checkDeviceAndScreen();

    // 
    window.addEventListener('resize', checkDeviceAndScreen);

    // 
    const disableRightClick = (e) => {
      e.preventDefault();
      return false;
    };
    document.addEventListener('contextmenu', disableRightClick);

    // 
    const disableDevTools = () => {
      // Detect F12 key
      document.addEventListener('keydown', (e) => {
        if (e.key === 'F12' || 
            // Detect Ctrl+Shift+I (Chrome/Edge/Firefox)
            (e.ctrlKey && e.shiftKey && (e.key === 'I' || e.key === 'i')) ||
            // Detect Ctrl+Shift+J (Chrome)
            (e.ctrlKey && e.shiftKey && (e.key === 'J' || e.key === 'j')) ||
            // Detect Ctrl+Shift+C (Chrome)
            (e.ctrlKey && e.shiftKey && (e.key === 'C' || e.key === 'c')) ||
            // Detect Cmd+Option+I (Mac)
            (e.metaKey && e.altKey && (e.key === 'I' || e.key === 'i'))) {
          e.preventDefault();
          return false;
        }
      });

      // Additional check for devtools opening
      const checkDevTools = () => {
        const widthThreshold = window.outerWidth - window.innerWidth > 160;
        const heightThreshold = window.outerHeight - window.innerHeight > 160;
        
        if (widthThreshold || heightThreshold) {
          // If devtools might be open, you could redirect or show a warning
          document.body.innerHTML = '<h1></h1>';
        }
      };

      // Run check periodically
      setInterval(checkDevTools, 1000);
    };

    // 
    if (window.location.hostname.includes('jp6bj3.github.io')) {
      disableDevTools();
    }

    const checkAuth = async () => {
      try {
        const savedSession = localStorage.getItem('authSession');
        const savedUser = localStorage.getItem('user');

        if (savedSession && savedUser) {
          const parsedUser = JSON.parse(savedUser);
          setUser(parsedUser);
          logger.debug('User session restored:', parsedUser.username);
        }
      } catch (error) {
        logger.error('Auth check error:', error);
      } finally {
        setIsLoading(false);
      }
    };

    checkAuth();

    // 
    return () => {
      document.removeEventListener('contextmenu', disableRightClick);
      window.removeEventListener('resize', checkDeviceAndScreen);
      // Cannot remove the interval here as it's not stored in a ref
    };
  }, []);

  const handleLogin = (userData) => {
    setUser(userData);
    logger.debug('User logged in:', userData.username);
  };

  const handleLogout = () => {
    localStorage.removeItem('authSession');
    localStorage.removeItem('user');
    setUser(null);
    setShowAdmin(false);
    queryClient.clear();
    logger.debug('User logged out');
  };

  // 
  const canLogout = user && (user.username === 'admin' || user.username === 'una.yang');

  // 
  if (deviceStatus.shouldBlockAccess) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center bg-gray-50 p-4 text-center">
        <h2 className="text-2xl font-bold mb-4"></h2>
        <p className="mb-4"> 1000x700 </p>
        <div className="mt-4 p-4 bg-white rounded shadow-sm text-left max-w-md">
          <h3 className="font-semibold mb-2">:</h3>
          <ul className="text-sm">
            <li><span className="font-medium">:</span> {deviceInfo.type}</li>
            <li><span className="font-medium">:</span> {deviceInfo.screenSize}</li>
            <li><span className="font-medium">:</span> {deviceInfo.hasSmallScreen}</li>
            <li><span className="font-medium">:</span> {deviceInfo.touchEnabled}</li>
          </ul>
        </div>
      </div>
    );
  }

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-lg">...</div>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50 p-4">
        <Auth onLogin={handleLogin} />
      </div>
    );
  }

  return (
    <EnhancedToastProvider>
    <QueryClientProvider client={queryClient}>
      <ListProvider>
      <ScreenshotPrevention>
        <div className="min-h-screen bg-gray-50">
          {/*  */}
          <nav className="bg-white shadow-sm">
            <div className="px-4">
              <div className="flex justify-end h-16">
                <div className="flex items-center space-x-6">
                  <span className="text-lg font-semibold">
                    , {user.username}
                  </span>
                  {user.username === 'admin' && (
                    <p
                      onClick={() => setShowAdmin(!showAdmin)}
                      className="flex items-center p-5 cursor-pointer hover:bg-gray-100"
                    >
                      {showAdmin ? '' : ''}
                    </p>
                  )}
                  {canLogout && (
                    <p
                      onClick={handleLogout}
                      className="flex items-center p-5 cursor-pointer hover:bg-gray-100"
                    >
                      
                    </p>
                  )}
                </div>
              </div>
            </div>
          </nav>

          {/*  */}
          {user.username === 'admin' && showAdmin ? (
            <Suspense fallback={<div className="min-h-screen flex items-center justify-center">...</div>}>
              <AdminManagement />
            </Suspense>
          ) : (
            <AuthProvider>
              <ProductContent />
            </AuthProvider>
          )}
        </div>
        </ScreenshotPrevention>
      </ListProvider>
    </QueryClientProvider>
    </EnhancedToastProvider>

  );
}

export default App;